<template>
<div>
  <v-container>
    <custom-snackbar ref="customSnackbar"></custom-snackbar>
    <v-card class="mb-2">
      <v-card-title class="headline font-weight-regular blue-grey white--text">Practice Maintenance</v-card-title>
      <v-card-text>
        <v-subheader class="pa-0">Practice Lookup<i class="material-icons" @click="newPractice()" style="padding-left:8px;">add_circle_outline</i></v-subheader>
        <v-autocomplete
          v-model="selectedId"
          :items=practices
          persistent-hint
          item-text="group_practice"
          item-value="id"
          @change="selectPractice(selectedId)"
        >
        </v-autocomplete>
      </v-card-text>
    </v-card>
    <v-card v-if="showPracticeProfile">
      <v-card-title class="headline font-weight-regular blue-grey white--text">Practice Profile</v-card-title>
      <v-card-text>
        <v-form ref="form">
          <v-text-field v-model="practice.action" label="Action Type" v-show="false"></v-text-field>
          <v-container fluid>
            <v-layout row wrap> 
              <v-flex xs12 md12>
                <v-text-field v-model="practice.group_practice" v-validate="'required'" 
                data-vv-name="Group Practice" label="Group Practice" required :error-messages="errors.collect('Group Practice')"></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row wrap> 
              <v-flex xs12 md12>
                <v-text-field v-model="practice.group_name" v-validate="'required'" 
                data-vv-name="Group Name" label="Group Name" required :error-messages="errors.collect('Group Name')"></v-text-field>
              </v-flex>
            </v-layout>       
            <v-layout row wrap> 
              <v-flex xs12 md7>
                <v-text-field v-model="practice.contact_name"  
                data-vv-name="Contact Name" label="Contact Name" :error-messages="errors.collect('Contact Name')"></v-text-field>
              </v-flex>
              <v-flex xs12 md5>
                <v-text-field v-model="practice.contact_phone"  
                data-vv-name="Contact Phone" label="Contact Phone" :error-messages="errors.collect('Contact Phone')"></v-text-field>
              </v-flex>
            </v-layout>       
            <v-layout row wrap>               
              <v-flex xs12 md12>
                <v-text-field v-model="practice.contact_email"  
                data-vv-name="Contact Email" label="Contact Email" :error-messages="errors.collect('Contact Email')"></v-text-field>
              </v-flex>
            </v-layout> 
            <v-layout row wrap>               
              <v-flex xs12 md12>
                <v-text-field v-model="practice.practice_director"  
                data-vv-name="Practice Director" label="Practice Director" :error-messages="errors.collect('Practice Director')"></v-text-field>
              </v-flex>
            </v-layout>  
            <v-layout row wrap>               
              <v-flex xs12 md6>
                <v-text-field v-model="practice.tax_id"  
                data-vv-name="Tax ID" label="Tax ID" :error-messages="errors.collect('Tax ID')"></v-text-field>
              </v-flex>
              <v-flex xs12 md6>
                  <v-select
                    v-model="practice.auditor_id"
                    :items="auditors"
                    item-text="fullname"
                    item-value="id"
                    label="Auditor"
                  ></v-select>           
                <!-- <v-text-field v-model="practice.auditor_id"  
                data-vv-name="Auditor" label="Auditor" :error-messages="errors.collect('Auditor')"></v-text-field> -->
              </v-flex>
            </v-layout>                                    
            <!-- <v-layout row wrap> 
              <v-flex xs12 md6>
                <v-text-field v-model="practice.practice_name" v-validate="'required'" 
                data-vv-name="Name" label="Name" required :error-messages="errors.collect('Name')"></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row wrap> 
              <v-flex xs12 md6>
                <v-text-field v-model="practice.chief_administrative_officer"  
                data-vv-name="Chief Administrative Officer" label="Chief Administrative Officer" :error-messages="errors.collect('Chief Administrative Officer')"></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row wrap> 
              <v-flex xs12 md6>
                <v-text-field v-model="practice.chief_compliance_officer" 
                data-vv-name="Chief Compliance Officer" label="Chief Compliance Officer" :error-messages="errors.collect('Chief Compliance Officer')"></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row wrap> 
              <v-flex xs12 md6>
                <v-text-field v-model="practice.chief_medical_officer" 
                data-vv-name="Chief Medical Officer" label="Chief Medical Officer" :error-messages="errors.collect('Chief Medical Officer')"></v-text-field>
              </v-flex>
            </v-layout> -->
            <v-layout row wrap> 
              <v-flex xs12 sm8 md4>
                <v-btn @click="savePractice()">Save</v-btn>
                <v-btn @click="cancelPractice()">Cancel</v-btn>
              </v-flex>
              <v-flex xs12 sm4 md2>
                <v-btn @click="deletePractice()">Delete</v-btn>
              </v-flex>
            </v-layout>
          </v-container>

        </v-form>
      </v-card-text>
    </v-card>
  </v-container>
  <confirmation-dialog ref="confirmationDialog" :title="confirmComponentMessage.title" :message="confirmComponentMessage.message"></confirmation-dialog>

  <!-- <pre>{{ currentUser }}</pre>
  <pre>{{ audits }}</pre> -->
</div>
</template>

<script>
import ConfirmationDialog from "@/components/ConfirmationDialog";
import CustomSnackbar from "@/components/Snackbar";

const initializePractice = () => {
  return {
    id: "",
    group_name: "",
    group_practice: "",
    contact_name: "",
    contact_phone: "",
    contact_email: "",
    auditor_id: -1,
    tax_id: "",
    practice_director: ""
  };
};

export default {
  name: "Main",
  components: {
    ConfirmationDialog,
    CustomSnackbar
  },
  data: () => ({
    practice: {},
    selectedId: null,
    showPracticeProfile: false,
    confirmComponentMessage: { title: "", message: "" }
  }),
  computed: {
    practices() {
      return this.$store.getters.PRACTICE;
    },
    auditors() {
      return this.$store.getters.ALL_USERS;
    }
  },
  methods: {
    selectPractice: function(id) {
      if (id) {
        this.practice = Object.assign({}, this.practices.find(p => p.id === id));
        this.practice.action = "update"; // updating an existing practice
        this.showPracticeProfile = true;
      }
    },
    newPractice: function() {
      this.practice = initializePractice();
      this.practice.action = "new";
      this.showPracticeProfile = true;
    },
    savePractice: function() {
      this.$validator.validateAll().then(result => {
        if (!result) {
          return false;
        } else {
          let dispatchType = "";
          if (this.practice.action === "new") {
            dispatchType = "NEW_PRACTICE";
          } else {
            dispatchType = "UPDATE_PRACTICE";
          }
          this.$store
            .dispatch(dispatchType, this.practice)
            .then(data => {
              if (data === true) {
                this.showMessage("success", "Record sucessfully saved");
                if (this.practice.action === "new") {
                  this.showPracticeProfile = false;
                  this.practice = initializePractice(); // re-initialize fields
                }
              } else {
                this.showMessage("error", "There was a problem saving your changes.  MSG - " + data);
              }
            })
            .catch(err => {
              console.log("err - ", err);
              this.showMessage("error", "There was a problem saving your changes.  MSG - " + err);
            });
        }
      });
    },
    deletePractice: function() {
      this.confirmComponentMessage.title = "Confirm Deletion?";
      this.confirmComponentMessage.message = "Are you sure you want to delete this record?";
      this.$refs.confirmationDialog
        .confirmation()
        .then(res => {
          if (res) {
            this.showPracticeProfile = false;
            this.$store.dispatch("DELETE_PRACTICE", this.practice).then(data => {
              if (data === true) {
                this.showMessage("success", "Record sucessfully deleted");
                this.selectedId = null;
                this.showPracticeProfile = false;
              } else {
                this.showMessage("error", "There was a problem deleting the record.  MSG - " + data);
              }
            });
          }
        })
        .catch(err => {
          console.log("err - ", err);
          this.showMessage("error", "There was a problem sdeleting the record.  MSG - " + err);
        });
    },
    cancelPractice: function() {
      this.confirmComponentMessage.title = "Cancel?";
      this.confirmComponentMessage.message = "Are you sure you want to cancel your changes?";
      this.$refs.confirmationDialog.confirmation().then(res => {
        if (res) {
          this.selectedId = null;
          this.showPracticeProfile = false;
        }
      });
    },
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, false);
    }
  }
};
</script>

<style>
i:hover {
  cursor: pointer;
}
</style>
